t":["<template>\n    <div class=\"select-text\">Select modules to deploy</div>\n    <div class=\"top-text\">\n        <span\n            class=\"select-modules-checkbox mdi mdi-minus-box mdi-24px me-2 mb-1 ms-0\"\n            v-if=\"enabledModules.length === checkedModules.length\"\n            @click=\"setAllCheckStatus(false)\"\n        ></span>\n        <span\n            class=\"select-modules-checkbox mdi mdi-plus-box mdi-24px me-2 mb-1 ms-0\"\n            v-if=\"enabledModules.length !== checkedModules.length\"\n            @click=\"setAllCheckStatus(true)\"\n        ></span>\n        <span class=\"bold-number\">{{ checkedModules.length }}</span>\n        <span class=\"top-text-description\" style=\"color: #616161\"> of </span>\n        <span class=\"bold-number\">{{ modules.length }}</span>\n        <span class=\"top-text-description\"> modules selected</span>\n    </div>\n    <div style=\"display: flex\">\n        <div class=\"treeviewCard\">\n            <tree :nodes=\"treeViewNodes\" :config=\"config\" @nodeChecked=\"setSelectedModules\" @nodeUnchecked=\"setSelectedModules\">\n                <template #before-input=\"{ node }\">\n                    <span :class=\"'mdi mdi-puzzle module-color icon-position' + (node.state.disabled ? ' deploy-screen-disabled-icon' : '')\" />\n                </template>\n                <template #after-input=\"{ node }\">\n                    <span class=\"warning-text\" v-if=\"node.state.disabled\">(Deploy Settings is not provided!)</span>\n                </template>\n            </tree>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport tree from \"vue3-treeview\";\nimport \"vue3-treeview/dist/style.css\";\nimport { computed, ref } from \"vue\";\nimport { IModule } from \"../../../../../../domain/model/models\";\nimport IDeployScreenProps, { ITreeViewNode } from \"../../../../../../domain/presentation/dialogs/IDeployScreenProps\";\n\ninterface IData {\n    deployScreenProps: IDeployScreenProps;\n    modules: Array<IModule>;\n}\n\ntype ITreeViewData = Record<string, ITreeViewNode>;\nconst props = defineProps<IData>();\nconst modules = ref<Array<IModule>>(props.modules.filter((module) => !!module.children.length));\nconst roots = modules.value.map((module) => module.ID);\nconst treeViewNodes = ref<ITreeViewData>(props.deployScreenProps.moduleSelectionState);\n\nif (!Object.keys(treeViewNodes.value).length) {\n    modules.value.forEach((item) => {\n        treeViewNodes.value[item.ID] = {\n            ID: item.ID,\n            text: item.name,\n            name: item.name,\n            type: item.objectType,\n            state: {\n                checked: item.deployParameters ? item.deployParameters.gitServiceRepoId != 0 : false,\n                disabled: item.deployParameters ? item.deployParameters.gitServiceRepoId == 0 : true,\n            },\n        };\n    });\n}\n\nconst checkedModules = computed(() =>\n    Object.keys(treeViewNodes.value)\n        .map((key) => treeViewNodes.value[key])\n        .filter((item) => item.state.checked)\n);\n\nconst enabledModules = computed(() =>\n    Object.keys(treeViewNodes.value)\n        .map((key) => treeViewNodes.value[key])\n        .filter((item) => !item.state.disabled)\n);\n\nconst config = ref({\n    roots,\n    checkboxes: true,\n    dragAndDrop: false,\n    editable: false,\n    checkMode: 0,\n    disabledClass: \"deploy-screen-disabled-node\",\n});\n\nfunction setSelectedModules() {\n    props.deployScreenProps.modulesChanged = true;\n    props.deployScreenProps.selectedModules = modules.value.filter((module) =>\n        checkedModules.value.some((checkedModule) => checkedModule.ID === module.ID && checkedModule.state.checked)\n    );\n\n    props.deployScreenProps.selectedAssets = props.deployScreenProps.selectedModules.map(\n        (module) =>\n            ({\n                ID: module.ID,\n                name: module.name,\n                text: module.name,\n                type: module.objectType,\n                children: module.children,\n                state: { checked: true },\n                version: undefined,\n            } as ITreeViewNode)\n    );\n\n    props.deployScreenProps.stepper.isTabValid = !!props.deployScreenProps.selectedModules.length;\n}\n\nsetSelectedModules();\n\nfunction setAllCheckStatus(selected: boolean) {\n    Object.keys(treeViewNodes.value).forEach(\n        (key) => (treeViewNodes.value[key].state.checked = selected && !treeViewNodes.value[key].state.disabled)\n    );\n    setSelectedModules();\n}\n</script>\n\n<style scoped>\n@import url(\"../../../../../../styles/IDE3.css\");\n\n.select-text {\n    position: absolute;\n    top: 141px;\n    left: 62px;\n    width: 370px;\n    height: 26px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--grey-darken-1);\n    text-align: left;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #757575;\n    opacity: 1;\n}\n\n.top-text {\n    position: absolute;\n    top: 185px;\n    left: 62px;\n}\n\n.bold-number {\n    font: var(--unnamed-font-style-normal) normal bold var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--grey-darken-4);\n    text-align: left;\n    font: normal normal bold 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #212121;\n}\n\n.top-text-description {\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--grey-darken-1);\n    text-align: left;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #757575;\n}\n\n.select-modules-checkbox {\n    width: 18px;\n    height: 18px;\n    color: #151233;\n    /* UI Properties */\n    border-radius: 50%;\n    opacity: 1;\n}\n\n.treeviewCard {\n    position: absolute;\n    top: 219px;\n    left: 62px;\n    width: 1293px;\n    height: 392px;\n    /* UI Properties */\n    background: var(--white) 0% 0% no-repeat padding-box;\n    border: 1px solid var(--grey-lighten-2);\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #e0e0e0;\n    opacity: 1;\n    overflow: auto;\n}\n\n.treeviewCard::-webkit-scrollbar {\n  width: 12px;               \n}\n\n.treeviewCard::-webkit-scrollbar-track {\n  background: transparent;        \n}\n\n.treeviewCard::-webkit-scrollbar-thumb {\n  background-color: #D0D1E9;    \n  border-radius: 16px;   \n}\n\n.icon-position {\n    margin-right: -8px;\n    margin-left: 10px;\n}\n\n.warning-text {\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) / var(--unnamed-line-spacing-21)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--grey-darken-4);\n    text-align: left;\n    font: normal normal normal 16px/21px Roboto;\n    letter-spacing: 0px;\n    color: red;\n    opacity: 1;\n}\n</style>\n"],"sourceRoot":""}]);
