ore-input=\"{ node }\">\n                <span v-if=\"node.type == 'module'\" class=\"mdi mdi-puzzle module-color icon-position\" />\n                <span v-if=\"node.type == 'qjson'\" class=\"mdi mdi-palette qjson-color icon-position\" />\n                <span v-if=\"node.type == 'yaml'\" class=\"mdi mdi-palette yaml-color icon-position\" />\n                <span v-if=\"node.type == 'js'\" class=\"mdi mdi-palette js-color icon-position\" />\n                <span v-if=\"node.type == 'process'\" class=\"mdi mdi-auto-fix process-color icon-position\" />\n                <span v-if=\"node.type == 'bpmn'\" class=\"mdi mdi-vector-polyline bpmn-color icon-position\" />\n                <span v-if=\"node.type == 'entityDesigner'\" class=\"mdi mdi-database entity-designer-color icon-position\" />\n            </template>\n            <template #after-input=\"{ node }\" style=\"display: flex; align-items: flex-end\">\n                <div class=\"col align-self-end\" v-if=\"node.version != undefined\">\n                    <div class=\"pencilIcon\">\n                        <h6 class=\"align-version tree-text ps-3 versionText\">\n                            <span v-if=\"node.version == 'System latest'\" class=\"versionBadge latestVersionBadge\">* </span>\n                            {{ node.version + \"   version\" }}\n                            <i class=\"mdi mdi-pencil-outline ms-1\" @click=\"versionDialog(node)\"></i>\n                        </h6>\n                    </div>\n                </div>\n                <div v-if=\"node.type === 'module'\">\n                    <div class=\"selected-module-count\">\n                        <span class=\"selected-items-count\">\n                            (<span class=\"selected-items-count-bold\">\n                                {{ node.children.filter((children:any) => treeViewNodes[children].state.checked ).length}}\n                            </span>\n                            of\n                            <span class=\"selected-items-count-bold\">\n                                {{ node.children.length }}\n                            </span>\n                            models selected)\n                        </span>\n                    </div>\n                </div>\n            </template>\n        </tree>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport tree from \"vue3-treeview\";\nimport \"vue3-treeview/dist/style.css\";\nimport { defineAsyncComponent, ref } from \"vue\";\nimport { container } from \"../../../../../../domain/core/diContainer\";\nimport IDeployScreenProps from \"../../../../../../domain/presentation/dialogs/IDeployScreenProps\";\nimport { ITreeViewNode } from \"../../../../../../domain/presentation/dialogs/IDeployScreenProps\";\nimport { IDialog } from \"../../../../../../domain/presentation/IDialog\";\n\ninterface IData {\n    deployScreenProps: IDeployScreenProps;\n}\n\ntype ITreeViewData = Record<string, ITreeViewNode>;\nconst props = defineProps<IData>();\nconst dialog = container.resolve<IDialog>(IDialog);\nconst selectedAssets = ref<Array<ITreeViewNode>>(props.deployScreenProps.selectedAssets);\nconst roots = selectedAssets.value.map((item) => item.ID);\nconst treeViewNodes = ref<ITreeViewData>(props.deployScreenProps.assetSelectionState);\n\nfunction iterateItemsAndFindChildren(items: Array<any>) {\n    items.forEach((item) => {\n        const curNode: ITreeViewNode = (treeViewNodes.value[item.ID] = {\n            ID: item.ID,\n            text: item.name,\n            name: item.name,\n            type: item.type || item.modelType || item.objectType,\n            state: { checked: true, disabled: false },\n            version: item.type != \"folder\" && item.type != \"module\" ? \"System latest\" : undefined,\n        });\n        if (!item.children) {\n            return;\n        }\n\n        curNode.children = item.children.map((child: any) => child.ID);\n        iterateItemsAndFindChildren(item.children);\n    });\n}\n\nif (!Object.keys(treeViewNodes.value).length || props.deployScreenProps.modulesChanged) {\n    props.deployScreenProps.selectedAssets.forEach((asset) => {\n        if (!Object.keys(treeViewNodes.value).includes(asset.ID)) {\n            iterateItemsAndFindChildren(selectedAssets.value);\n        }\n    });\n    props.deployScreenProps.modulesChanged = false;\n}\n\nselectedAssets.value.forEach((asset) => {\n    asset.children = asset.children?.map((child) => treeViewNodes.value[child.ID]);\n});\n\nconst config = ref({\n    roots,\n    checkboxes: true,\n    dragAndDrop: false,\n    editable: false,\n    checkMode: 0,\n});\n\nfunction setSelectedAssets() {\n    selectedAssets.value.map((asset) => (asset.state.checked = asset.children!.some((children) => children.state.checked)));\n    props.deployScreenProps.stepper.isTabValid = props.deployScreenProps.selectedAssets.some((asset) => asset.state.checked);\n}\n\nsetSelectedAssets();\n\nfunction versionDialog(clickedNode: ITreeViewNode) {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(\"../../../application/modelVersionDialog.vue\")),\n        { closable: true, title: \"Change Version\", width: \"900px\", height: \"auto\", overflow: \"auto\" },\n        { clickedNode }\n    );\n}\n</script>\n\n<style scoped>\n.select-text {\n    position: absolute;\n    top: 141px;\n    left: 62px;\n    width: 370px;\n    height: 26px;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--grey-darken-1);\n    text-align: left;\n    font: normal normal normal 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #757575;\n    opacity: 1;\n}\n\n.treeviewCard {\n    position: absolute;\n    top: 182px;\n    left: 62px;\n    width: 1293px;\n    height: 429px;\n    /* UI Properties */\n    background: var(--white) 0% 0% no-repeat padding-box;\n    border: 1px solid var(--grey-lighten-2);\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #e0e0e0;\n    opacity: 1;\n    overflow: auto;\n}\n\n  .treeviewCard::-webkit-scrollbar {\n    width: 12px;               \n  }\n\n  .treeviewCard::-webkit-scrollbar-track {\n    background: transparent;        \n  }\n\n  .treeviewCard::-webkit-scrollbar-thumb {\n    background-color: #D0D1E9;    \n    border-radius: 16px;   \n  }\n\n.pencilIcon {\n    text-align: end;\n}\n\n.selected-items-count {\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) / var(--unnamed-line-spacing-21)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--grey-darken-1);\n    text-align: left;\n    font: normal normal normal 16px/21px Roboto;\n    letter-spacing: 0px;\n    color: #757575;\n}\n.selected-items-count-bold {\n    font: var(--unnamed-font-style-normal) normal bold var(--unnamed-font-size-16) / var(--unnamed-line-spacing-21) var(--unnamed-font-family-roboto);\n    text-align: left;\n    font: normal normal bold 16px/21px Roboto;\n    letter-spacing: 0px;\n    color: #151233;\n}\n\n.icon-properties {\n    background: var(--white) 0% 0% no-repeat padding-box;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #00000000;\n    opacity: 0;\n}\n\n.icon-position {\n    margin-right: -8px;\n    margin-left: 10px;\n}\n\n.qjson-color {\n    color: #449dd1;\n}\n\n.yaml-color {\n    color: #449dd1;\n}\n\n.js-color {\n    color: #449dd1;\n}\n\n.process-color {\n    color: #007c77;\n}\n\n.bpmn-color {\n    color: #007c77;\n}\n\n.entity-designer-color {\n    color: #e9724c;\n}\n</style>\n"],"sourceRoot":""}]);
